<template>
  <div class="content">
    <div class="header">
      <p>当前积分</p>
      <div class="current-integral">7500</div>
      <div class="integral-rule">积分规则</div>
    </div>
    <div class="title">
      <span></span>
      积分明细
    </div>
    <div class="integral-list">
      <list-details :list="integralList" />
    </div>
    <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          :error.sync="error"
          error-text="点击重新加载"
          :offset="100"
        ></van-list>
  </div>
</template>

<script>
import listDetails from "@/web/components/ListDetails";
export default {
  components: {
    listDetails
  },
  data() {
    return {
			loading: true, //加载
      finished: false, //是否加载完成
      error: false, //加载错误

      integralList: [
        {
          id: 1,
          way: "门店支付",
          time: "2019-01-29 13:46:47",
          add: 10
        },
        {
          id: 2,
          way: "门店支付",
          time: "2019-01-29 13:46:47",
          add: 10
        },
        {
          id: 3,
          way: "门店支付",
          time: "2019-01-29 13:46:47",
          add: 10
        },
        {
          id: 4,
          way: "商城支付",
          time: "2019-01-29 13:46:47",
          add: 12
        },
        {
          id: 5,
          way: "门店支付",
          time: "2019-01-29 13:46:47",
          add: 10
        },
        {
          id: 6,
          way: "商城支付",
          time: "2019-01-29 13:46:47",
          add: 5
        },
        {
          id: 7,
          way: "商城支付",
          time: "2019-01-29 13:46:47",
          add: 8
        },
        {
          id: 9,
          way: "门店支付",
          time: "2019-01-29 13:46:47",
          add: 10
        },
        {
          id: 10,
          way: "门店支付",
          time: "2019-01-29 13:46:47",
          add: 20
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
.content {
  .header {
    margin: 20px 24px;
    background: linear-gradient(
      187deg,
      rgba(217, 193, 134, 1) 0%,
      rgba(217, 193, 134, 1) 100%
    );
    border-radius: 10px;
    height: 220px;
    text-align: center;
    color: #fff;
    position: relative;
    overflow: hidden;
    p {
      font-size: 26px;
      margin: 47px 0 6px;
    }
    .current-integral {
      font-size: 50px;
    }
    .integral-rule {
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -22px;
      font-size: 22px;
      background-color: #ffffff;
      color: #d2ae55;
      width: 120px;
      height: 44px;
      line-height: 44px;
      opacity: 0.7;
      border-radius: 10px 0 0 10px;
    }
  }
  .title {
    margin: 38px 0 20px 24px;
    display: flex;
    align-items: center;
    font-size: 30px;
    color: #222;
    span {
      width: 6px;
      height: 26px;
      display: block;
      background-color: #d2ae55;
      margin-right: 20px;
    }
  }
  .integral-list {
    background: #fff;
		padding: 0 6px;
  }
}
</style>


